      // This method will be triggered when the selected option changes
      console.log('Selected option changed from', oldValue, 'to', newValue);
      if (newValue == 'Buy') {
        this.getListBuy();
      }
      if (newValue == 'Sell') {
        this.getListSell();
      }
      // You can call your method here or perform any other action
