    ...mapGetters('tradependindgStore', ['tradeData', 'userData', 'usersData', 'stateData', 'approveDate']),
    filterStates() {
      return this.states.filter((item) => {
        return item.title.toLowerCase().includes(this.state.toLowerCase());
      });
    },
